.Navigation {
  display: flex;
  flex-direction: row;
}
.Navigation li {
  padding: 20px;
  font-size: 20px;
  cursor: pointer;
  text-align: center;
}

.Navigation li a {
  text-decoration: none;
  color: black;
}
.Navigation.dark-mode li a {
  text-decoration: none;
  color: white;
}

@media only screen and (max-width: 767px) {
  .Navigation {
    display: none;
    flex-direction: column;
    margin-top: 20px;
    margin-bottom: 32px;
  }
  .Navigation li {
    width: 100vw;
    padding: 12px;
    padding-left: 0;
    padding-right: 0;
    font-size: 16px;
    cursor: pointer;
  }

  .Navigation.mobileMenuOpen {
    display: flex;
  }
}
