.Header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "TsukimiRoundedLight", "NotoSansKRLight", sans-serif;
  font-weight: 300;
  background-color: #ffffff;
  color: #000000;
  transition: background-color 0s, color 0s;
  position: sticky;
  top: 0;
}
.Header.dark-mode {
  background-color: #000000;
  color: #ffffff;
  /* transition: background-color 0s, color 0s; */
}
