.LogoLine {
  /* position: relative;
  top: 0;
  left: 0; */
}
.Logo {
  padding-top: 32px;
  padding-bottom: 8px;
}
.MobileBtn {
  display: none;
}

@media only screen and (max-width: 767px) {
  .LogoLine {
    height: 80px;
    width: 100vw;
    position: relative;
    top: 0;
    left: 0;
  }
  .Logo {
    width: calc(100vw - 16px);
    height: 48px;
    overflow: hidden;
    padding: 0;
    padding-top: 16px;
    padding-left: 16px;
    position: absolute;
    top: 0;
    left: 0;
  }
  .MobileBtn {
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 28px;
    padding: 26px;
  }
}
