.Container {
  font-family: "TsukimiRounded", "NotoSansKR", sans-serif;
  display: flex;
  flex-direction: column;
  max-width: 900px;
  margin: auto;
  padding: 80px;
}

@media only screen and (max-width: 767px) {
  .Container {
    padding: 16px;
    padding-top: 24px;
    width: calc(100%-32px);
  }
}
